import UsersComponent from './UsersComponent';
import UserCreateComponent from './UserCreateComponent';

const baseURL = '/usuarios';

export default [
  {
    path: `${baseURL}`,
    name: 'users',
    component: UsersComponent,
    meta:{
      title: 'Usurios',
      description: 'Lista de todos os usurios cadastrados no sistema.',
      requiresAuth: true
    }
  },
  {
    path: `${baseURL}/:id`,
    name: 'users_show',
    component: UserCreateComponent,
    props: true,
    meta:{
      title: 'Usurio selecionado',
      description: 'Exibe todos os dados do usurio selecionado.',
      requiresAuth: true
    }
  },
  {
    path: `${baseURL}/novo-usuario`,
    name: 'user_create',
    component: UserCreateComponent,
    meta:{
      title: 'Novo Usurio',
      description: 'Preencha todos os dados para cadastrar um novo usurio.',
      requiresAuth: true
    }
  }
]