import EnrollmentsComponent from './EnrollmentsComponent';
import EnrollmentCreateComponent from './EnrollmentCreateComponent';

const baseURL = '/Enrollments';

export default [
  {
    path: `${baseURL}`,
    name: 'enrollments',
    component: EnrollmentsComponent,
    meta:{
      title: 'Matrculas',
      description: 'Lista de todas as matrculas cadastradas no sistema.',
      requiresAuth: true
    }
  },
  {
    path: `${baseURL}/:id`,
    name: 'enrollment_show',
    component: EnrollmentCreateComponent,
    props: true,
    meta:{
      title: 'Matrcula selecionada',
      description: 'Exibe todos os dados da matrcula selecionada.',
      requiresAuth: true
    }
  },
  {
    path: `${baseURL}/novo-cliente`,
    name: 'enrollment_create',
    component: EnrollmentCreateComponent,
    meta:{
      title: 'Nova matrcula',
      description: 'Preencha todos os dados para cadastrar uma nova matrcula.',
      requiresAuth: true
    }
  }
]